.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.header h2 {
    margin-left: auto;
    margin-right: auto;
}

.footer {
    text-align: center;
    position: absolute;
    width: 100%;
    height: 4%;
    min-height: 25px;
    background-color: #282c34;
    color: white;
    bottom: 0;
}

.addEmp {
    margin: 50px auto 50px;
    width: 300px;
    padding: 20px;
    border-radius: 5px;
    border: solid 1px silver;
}